*{
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60 px;
}

input {
  font-size: 1.2rem;
  color: gray;
  width: 100%;
}

.card-grid {

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}


.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10 px;
  overflow: hidden;
}

.card p {
  padding: 5px ;
  text-align: center;
}

.card img {
  max-height: 170px ;
}
